<template>
  <div id="app">
    <MasterLayout>
      <transition :name="transitionName" mode="out-in">
        <router-view />
      </transition>
    </MasterLayout>
  </div>
</template>

<script>
export default {
  data() {
    return {
      transitionName: 'fade'
    }
  },
  watch: {
    $route(to, from) {
      const transitionName = this.$route.params.transition
      this.transitionName = transitionName
        ? transitionName
        : this.transitionName

      if (window.matchMedia('(max-width: 768px)').matches) {
        if (!this.$route.params.main) {
          this.$store.dispatch('setMenuState')
        }
        /* The viewport is at least 400 pixels wide */
        document.body.classList.remove('active')
      }
    }
  }
}
</script>

<style lang="scss">
@import 'scss/styles.scss';

#app {
  overflow-x: hidden;
}

.fade-enter-active,
.fade-leave-active {
  @include transition(opacity 0.325s ease-in-out);
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}

.slide-enter-active,
.slide-leave-active {
  @include transition(all 0.375s ease-in-out);
}

.slide-enter,
.slide-leave-to {
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
}

.scale-enter-active,
.scale-leave-active {
  @include transition(all 0.5s ease-in-out);
}

.scale-enter,
.scale-leave-to {
  opacity: 0;
  visibility: hidden;
  transform: scale(1.25);
}
</style>
