


















import { Component, Vue } from 'vue-property-decorator'
import HelloWorld from '@/components/HelloWorld.vue' // @ is an alias to /src

import fs from '@/firestore'

@Component({
  components: {
    HelloWorld
  },
  firestore: {
    users: fs.collection('users')
  }
})
export default class Home extends Vue {
  isActive: boolean = this.$store.state.isActive
  users: Array<object> = []
  name: string = ''

  mounted() {
    if (this.$store.state.isActive == true) {
      setTimeout(() => {
        this.$store.dispatch('setActive')
      }, 1750)
    }
  }

  addUsers() {
    fs.collection('users')
      .doc('ub6YIPMKnbTgY3kwQDPY')
      .set({ name: this.name })
      .then(() => {
        console.log('Successfully added user.')
      })
      .catch(() => {
        console.log('Sorry there was an error with adding a new user.')
      })
  }
}
