import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isActive: true,
    menuState: false
  },
  mutations: {
    SETACTIVE(state) {
      state.isActive = false
    },
    SETMENUSTATE(state) {
      state.menuState = !state.menuState
    }
  },
  actions: {
    setActive({ commit }) {
      commit('SETACTIVE')
    },
    setMenuState({ commit }) {
      commit('SETMENUSTATE')
    }
  }
})
