
























































































































import { Vue, Component } from 'vue-property-decorator'
import BaseCard from '@/components/BaseCard.vue'

@Component({
	components: {
		BaseCard
	}
})
export default class About extends Vue {
	isActive: boolean = false
	programming_languages: string[] = ['HTML', 'CSS', 'Javascript', 'PHP', 'Java']
	frameworks: string[] = [
		'Laravel',
		'JQuery',
		'React',
		'Vuejs',
		'Sass',
		'Bootstrap',
		'Bulma'
	]
	databases: string[] = ['MySQL']
	services: string[] = ['REST API']
	version_controls: string[] = ['Git']
	languages: string[] = ['English', 'Bahasa Malaysia']
	others: string[] = ['Technical Support', 'Google Analytics']
  hvrPrgLanguage: string = this.programming_languages[0]
	hvrFramework: string = this.frameworks[0]
	hvrDatabase: string = this.databases[0]
	hvrService: string = this.services[0]
	hvrVC: string = this.version_controls[0]
	hvrLanguage: string = this.languages[0]
	hvrOther: string = this.others[0]
	mounted() {
		this.$nextTick(function() {
			// Code that will run only after the
			// entire view has been rendered
			this.isActive = true
		})
	}
	beforeLeave() {
		this.isActive = false
	}

	showBadge(item: string, num: number): void {
		switch (num) {
			case 1:
				this.hvrPrgLanguage = item
				break
			case 2:
				this.hvrFramework = item
				break
			case 3:
				this.hvrDatabase = item
				break
			case 4:
				this.hvrService = item
				break
			case 5:
				this.hvrVC = item
				break
			case 6:
				this.hvrLanguage = item
				break
			case 7:
				this.hvrOther = item
				break
			default:
				break
		}
	}
}
