





























import { Vue, Component } from 'vue-property-decorator'

@Component
export default class NavMenu extends Vue {
  public links: Array<object> = [
    { name: 'home', transition: 'fade' },
    { name: 'skills', transition: '' },
    { name: 'experience', transition: '' },
    { name: 'education', transition: '' },
    { name: 'others', transition: '' }
  ]

  toggleMenu() {
    if (window.matchMedia('(max-width: 768px)').matches) {
      this.$store.dispatch('setMenuState')
      if (this.$store.state.menuState) {
        document.body.classList.add('active')
      } else {
        document.body.classList.remove('active')
      }
    }
  }
}
